(function ()
{
 'use strict';

 angular
         .module('app.explorer')
         .controller('ComponentLinearFurnitureController', ComponentLinearFurnitureController);

 /** @ngInject */
 function ComponentLinearFurnitureController(ComponentService, DialogComponentService, $stateParams, $rootScope)
 {
  var vm = this;
//DATA
  vm.notes = [];
  vm.componentId = $stateParams.id;
  //////////
  //METHODS
  vm.openComponentPinboardDialog = DialogComponentService.openComponentPinboardDialog;
  vm.openComponentPinboardDialog = DialogComponentService.openComponentPinboardDialog;
  vm.openComponentListboardDialog = DialogComponentService.openComponentListboardDialog;
  vm.openComponentChatsDialog = DialogComponentService.openComponentChatsDialog;
  vm.openComponentDiscussionsDialog = DialogComponentService.openComponentDiscussionsDialog;
  vm.openComponentGraphsDialog = DialogComponentService.openComponentGraphsDialog;
  vm.openComponentCalendarDialog = DialogComponentService.openComponentCalendarDialog;
  vm.startComponentStoryDialog = DialogComponentService.startComponentStoryDialog;
  //////////
  init();
  /**
   * Initialize
   */
  function init() {
   ComponentService.getComponent(vm.componentId, 0).then(function (data) {
    vm.notes = data.components;
    vm.notes.newComponentData = angular.copy(vm.defaultComponentData);
   });
  }
 }
})();
(function ()
{
 'use strict';

 angular
         .module('app.explorer')
         .controller('ComponentLinearCalendarController', ComponentLinearCalendarController);

 /** @ngInject */
 function ComponentLinearCalendarController(ComponentService, DialogService, $document, $mdDialog, $mdSidenav, $scope, $rootScope)
 {
  var vm = this;

  // Data
  vm.board = ComponentService.data;
  vm.eventSources = [];

  vm.calendarUiConfig = {
   calendar: {
    editable: true,
    eventLimit: true,
    header: '',
    handleWindowResize: false,
    aspectRatio: 1,
    dayNames: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
    dayNamesShort: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
    timezone: 'local',
    eventDurationEditable: false,
    defaultTimedEventDuration: '01:00:00',
    viewRender: function (view)
    {
     vm.calendarView = view;
     vm.calendar = vm.calendarView.calendar;
     vm.currentMonthShort = vm.calendar.getDate().format('MMM');
    },
    columnFormat: {
     month: 'ddd',
     week: 'ddd D',
     day: 'ddd D'
    },
    eventClick: function eventDetail(calendarEvent, ev)
    {
     vm.openCardDialog(ev, calendarEvent.idCard);
    },
    eventDrop: function (event)
    {
     vm.board.cards.getById(event.idCard).due = moment.utc(event.start).format('x');
    },
    selectable: true,
    selectHelper: true,
    dayClick: function (date, ev)
    {
     var offset = moment().utcOffset();
     var corrDate = '';

     if (offset < 0)
     {
      corrDate = moment.utc(date).subtract(offset, 'm').format('x');
     } else
     {
      corrDate = moment.utc(date).add(offset, 'm').format('x');
     }

     eventDialog(corrDate, ev);
    }
   }
  };

  // Methods
  vm.next = next;
  vm.prev = prev;
  vm.goToDate = goToDate;
  vm.openCardDialog = DialogService.openCardDialog;
  vm.toggleSidenav = toggleSidenav;

  //////////

  init();

  /**
   * Initialize
   */
  function init()
  {
   vm.cards = getScheduledCards();
   vm.eventSources[0] = vm.cards;
  }

  /**
   * Get scheduled cards and prepare
   * them to show on the calendar
   *
   * @returns {Array}
   */
  function getScheduledCards()
  {
   var cards = [];

   angular.forEach(vm.board.cards, function (card)
   {
    if (card.due)
    {
     cards.push({
      idCard: card.id,
      title: card.name,
      start: moment.utc(card.due, 'x'),
      due: card.due,
      backgroundColor: getEventBgColor(card.due)
     });
    }
   });

   return cards;
  }

  /**
   * Get background color
   *
   * @param cardDue
   * @returns {*}
   */
  function getEventBgColor(cardDue)
  {
   if (moment() > moment(cardDue, 'x'))
   {
    return '#F44336';
   }

   return '#4CAF50';
  }

  /**
   * Watch board changes
   */
  $scope.$watch('vm.board', function (current, old)
  {
   if (angular.equals(current, old))
   {
    return;
   }

   init();

  }, true);

  /**
   * Go to Date
   *
   * @param date
   */
  function goToDate(date)
  {
   vm.calendarView.calendar.gotoDate(date);
   $mdSidenav('scheduled-tasks-sidenav').close();
  }

  /**
   * Go to next on current view (week, month etc.)
   */
  function next()
  {
   vm.calendarView.calendar.next();
  }

  /**
   * Go to previous on current view (week, month etc.)
   */
  function prev()
  {
   vm.calendarView.calendar.prev();
  }

  /**
   * Event Dialog
   */
  function eventDialog(date, ev)
  {
   $mdDialog.show({
    templateUrl: 'src/app/main/apps/explorer/views/calendar/dialogs/event/event-dialog.html',
    controller: 'ExplorerCalendarEventDialogController',
    controllerAs: 'vm',
    parent: $document.find('#explorer'),
    targetEvent: ev,
    clickOutsideToClose: true,
    locals: {
     dueDate: date
    }
   });
  }

  /**
   * Toggle sidenav
   *
   * @param sidenavId
   */
  function toggleSidenav(sidenavId)
  {
   $mdSidenav(sidenavId).toggle();
  }
 }
})();
(function ()
{
 'use strict';

 angular
         .module('app.explorer')
         .controller('ComponentLinearActivitiesController', ComponentLinearActivitiesController);

 /** @ngInject */
 function ComponentLinearActivitiesController(ComponentService, $rootScope, $stateParams, $document, $timeout, $scope, $mdSidenav, $mdDialog)
 {
  var vm = this;

  // Data
  vm.componentId = $stateParams.id;
  vm.components = [];//ComponentService.data;
  vm.labels = [];//LabelsService.data;
  vm.search = '';
  vm.searchToolbar = false;
  vm.filters = {
   archive: false
  };
  vm.labelFilterIds = [];
  vm.componentListType = 'components';

  // Methods
  vm.filterChange = filterChange;
  vm.openSearchToolbar = openSearchToolbar;
  vm.closeSearchToolbar = closeSearchToolbar;
  vm.deleteLabel = deleteLabel;
  vm.toggleSidenav = toggleSidenav;
  //vm.addNewLabel = LabelsService.addLabel;

  //////////

  init();

  // ******************************
  // Internal methods
  // ******************************

  /**
   * Change Components Filter
   * @param type
   */
  function filterChange(type) {

   vm.componentListType = type;

   if (type === 'components')
   {
    vm.labelFilterIds = [];
    vm.filters = {
     archive: false
    };
   } else if (type === 'archive')
   {
    vm.labelFilterIds = [];
    vm.filters = {
     archive: true
    };
   } else if (type === 'reminders')
   {
    vm.labelFilterIds = [];
    vm.filters = {
     reminder: '',
     archive: false
    };
   } else if (angular.isObject(type))
   {
    vm.labelFilterIds = [];
    vm.labelFilterIds.push(type.id);
   }

   $mdSidenav('main-sidenav').close();

  }



  /**
   * Delete Label
   * @param ev
   * @param label
   */
  function deleteLabel(ev, label)
  {
   var confirm = $mdDialog.confirm()
           .title('Are you sure want to delete the label?')
           .htmlContent('The "' + label.name + '" label will be deleted permanently.')
           .ariaLabel('delete label')
           .targetEvent(ev)
           .ok('OK')
           .cancel('CANCEL');

   $mdDialog.show(confirm).then(function ()
   {
    //LabelsService.deleteLabel(label);
   });
  }

  /**
   * Open Search Toolbar
   */
  function openSearchToolbar()
  {
   vm.searchToolbar = true;

   $timeout(function ()
   {
    angular.element($document.find('#search-components-input')).trigger('focus');

   });

   $document.on('keyup', escKeyEvent);
  }

  /**
   * Close Search Toolbar
   */
  function closeSearchToolbar()
  {
   $scope.$evalAsync(function ()
   {
    vm.searchToolbar = false;
    vm.search = '';
   });

   $document.off('keyup', escKeyEvent);
  }

  /**
   * Escape key event
   */
  function escKeyEvent(e)
  {
   if (e.keyCode === 27)
   {
    closeSearchToolbar();
   }
  }

  /**
   * Toggle sidenav
   *
   * @param sidenavId
   */
  function toggleSidenav(sidenavId)
  {
   $mdSidenav(sidenavId).toggle();
  }

  /**
   * Array prototype
   *
   * Get by id
   *
   * @param value
   * @returns {T}
   */
  Array.prototype.getById = function (value)
  {
   return this.filter(function (x)
   {
    return x.id === value;
   })[0];
  };

  /**
   * Initialize
   */
  function init()
  {
   ComponentService.getComponent(vm.componentId, 0).then(function (data) {
    vm.components = data.components;
    vm.components.newComponentData = angular.copy(vm.defaultComponentData);
   });
  }


 }

})();
(function ()
{
    'use strict';

    angular
        .module('app.explorer')
        .controller('ExplorerCalendarEventDialogController', ExplorerCalendarEventDialogController);

    /** @ngInject */
    function ExplorerCalendarEventDialogController($mdDialog, dueDate, ComponentService, msUtils)
    {
        var vm = this;

        // Data
        vm.board = ComponentService.data;
        vm.dueDate = dueDate;
        vm.newCard = {
            name  : '',
            listId: ''
        };
        vm.selectedCards = [];

        // Methods
        vm.exists = msUtils.exists;
        vm.toggleInArray = msUtils.toggleInArray;
        vm.closeDialog = closeDialog;
        vm.addNewCard = addNewCard;
        vm.assignDueDate = assignDueDate;

        //////////

        /**
         * Close Dialog
         */
        function closeDialog()
        {
            $mdDialog.hide();
        }

        /**
         * Add New Card
         */
        function addNewCard()
        {
            var newCardId = msUtils.guidGenerator();

            vm.board.cards.push({
                'id'               : newCardId,
                'name'             : vm.newCard.name,
                'description'      : '',
                'idAttachmentCover': '',
                'idMembers'        : [],
                'idLabels'         : [],
                'attachments'      : [],
                'subscribed'       : false,
                'checklists'       : [],
                'checkItems'       : 0,
                'checkItemsChecked': 0,
                'comments'         : [],
                'activities'       : [],
                'due'              : vm.dueDate
            });

            vm.board.lists.getById(vm.newCard.listId).idCards.push(newCardId);

            // Reset the newCard object
            vm.newCard = {
                name: '',
                listId: ''
            };

            closeDialog();
        }

        /**
         * Assign Due Date
         */
        function assignDueDate()
        {
            angular.forEach(vm.selectedCards, function (cardId)
            {
                vm.board.cards.getById(cardId).due = vm.dueDate;
            });

            vm.selectedCards = [];

            closeDialog();
        }
    }
})();
(function ()
{
 'use strict';

 angular
         .module('app.explorer')
         .controller('AppComponentsPersonalTabController', AppComponentsPersonalTabController);

 /** @ngInject */
 function AppComponentsPersonalTabController(level_categories, ComponentService, $scope, $rootScope)
 {
  var vm = this;

  //Data
  vm.swipeTypes = level_categories.swipe;
  vm.component;
  vm.swipes;
  ////////

  //Methods
  vm.getSwipe = getSwipe;
  vm.getSwipes = getSwipes;
  vm.createSwipe = createSwipe;
  ////////

  //Init
  getSwipe();
  getSwipes($rootScope.user.id);
  ////////

  /**
   * Get a random component
   * @returns
   */
  function getSwipe() {
   ComponentService.getRandomBoard().then(function (response) {
    vm.component = response;
    addComponentPlaceholderImage(vm.component);
   });
  }

  /**
   * Get all user's swipes
   *
   * @returns
   */
  function getSwipes(creatorId) {
   if (creatorId) {
    ComponentService.getComponentBookmarks(creatorId).then(function (response) {
     vm.swipes = response;
     angular.forEach(vm.swipes, function (swipe) {
      addComponentPlaceholderImage(swipe.component);
     });
    });
   }
  }

  /**
   * Creates a swipe component
   *
   * @param {type} levelId swipe type id
   * @returns {undefined}
   */
  function createSwipe(levelId) {
   var data = {
    componentId: vm.component.id,
    levelId: levelId
            // description: ""
   };
   ComponentService.createComponentBookmark(data).then(function (response) {
    getSwipe();
   });
  }

  /**
   * Adds a placeholder css generated image to the component
   *
   * @param {type} component a component to be added a placeholder image
   * @returns {undefined}
   */
  function addComponentPlaceholderImage(component) {
   if (component.component_picture_url || component.component_picture_url === 'default.png') {
    component.component_placeholder_style = {background: $rootScope.generateBackgroundPattern()};
   }
  }

  $rootScope.headerStyle = {background: $rootScope.generateBackgroundPattern()};
  $rootScope.appName = "Explorer Swipe";

 }
})();
