(function ()
{
    'use strict';

    angular
        .module('app.components')
        .controller('msComponentItemController', msComponentItemController)
        .directive('msComponentItem', msComponentItemDirective);

    /** @ngInject */
    function msComponentItemController($document)
    {
        var vm = this;
        var fontSizes = {
            '1': {
                fontSize  : 14,
                lineHeight: 19
            },
            '2': {
                fontSize  : 18,
                lineHeight: 28
            },
            '3': {
                fontSize  : 24,
                lineHeight: 36
            },
            '4': {
                fontSize  : 36,
                lineHeight: 48
            },
            '5': {
                fontSize  : 48,
                lineHeight: 60
            }
        };

        vm.element = [];
        vm.noFontResize = false;

        // Methods
        vm.updateDescriptionFontSize = updateDescriptionFontSize;

        //////

        function updateDescriptionFontSize()
        {
            if ( vm.noFontResize || !vm.component.description || vm.component.description === '' )
            {
                return;
            }

            var fontSize = '';
            var descWidth = Math.floor(vm.columnWidth - 52); // 48px paddings, + 4px tolerance
            var tmp = angular.element('<div style="visibility:hidden; font-size:10px;line-height: 10px;position:absolute;z-index:-1;white-space:pre-wrap;word-wrap: break-word;"></div>');
            tmp.appendTo($document.find('body'));

            fontSize = checkLineCount(sizeFromMaxWordCount());

            angular.element(vm.element).find('md-description').attr('font-size', fontSize);

            tmp.remove();

            function sizeFromMaxWordCount()
            {
                var words = vm.component.description.split(' ');

                var wordLengths = words.map(function (word)
                {
                    return tmp.text(word)[0].clientWidth;
                });

                var maxWordLength = Math.max.apply(Math, wordLengths);

                var maxPx = Math.floor((descWidth * 10) / maxWordLength);

                if ( maxPx < 18 )
                {
                    fontSize = 1;
                }
                else if ( 18 <= maxPx && maxPx < 24 )
                {
                    fontSize = 2;
                }
                else if ( 24 <= maxPx && maxPx < 36 )
                {
                    fontSize = 3;
                }
                else if ( 36 <= maxPx && maxPx < 48 )
                {
                    fontSize = 4;

                }
                else if ( maxPx >= 48 )
                {
                    fontSize = 5;
                }
                return fontSize;
            }

            function checkLineCount(sizeFromMaxWord)
            {
                var size = fontSizes[sizeFromMaxWord];
                var result;

                tmp.text(vm.component.description);
                tmp.width(descWidth);
                tmp.css({
                    'line-height': size.lineHeight + 'px',
                    'font-size'  : size.fontSize + 'px'
                });

                var lineCount = tmp[0].clientHeight / size.lineHeight;

                if ( 4 < lineCount && lineCount < 6 )
                {
                    result = 4;
                }
                else if ( 6 <= lineCount && lineCount < 9 )
                {
                    result = 3;
                }
                else if ( 9 <= lineCount && lineCount < 11 )
                {
                    result = 2;
                }
                else if ( 11 <= lineCount )
                {
                    result = 1;
                }
                else
                {
                    result = sizeFromMaxWord;
                }
                return result;
            }
        }
    }

    /** @ngInject */
    function msComponentItemDirective()
    {
        return {
            restrict        : 'A',
            controller      : 'msComponentItemController as MsComponentItem',
            templateUrl     : 'src/app/main/apps/components/directives/ms-component-item/ms-component-item.html',
            require         : ['msComponentItem', '^msMasonry'],
            bindToController: {
                msComponentItem: '='
            },
            link            : function (scope, element, attributes, controllers)
            {

                var MsComponentItem = controllers[0];
                var msMasonry = controllers[1];

                MsComponentItem.element = element;
                MsComponentItem.component = MsComponentItem.msComponentItem;


                if ( angular.isDefined(attributes.noFontResize) )
                {
                    MsComponentItem.noFontResize = true;
                }

                scope.$watch('MsComponentItem.msComponentItem', function (newVal, oldVal)
                {
                    if ( newVal !== oldVal )
                    {
                        MsComponentItem.component = newVal;
                    }
                });

                scope.$on('msMasonryItem:startReLayout', function ()
                {
                    if ( MsComponentItem.noFontResize || MsComponentItem.columnWidth === msMasonry.columnWidth )
                    {
                        return;
                    }

                    MsComponentItem.columnWidth = msMasonry.columnWidth;

                    MsComponentItem.updateDescriptionFontSize();
                });
            }
        };
    }
})();
(function ()
{
 'use strict';

 angular
         .module('app.components')
         .controller('msNewComponentController', msNewComponentController)
         .directive('msNewComponentButton', msNewComponentButtonDirective)
         .directive('msNewComponent', msNewComponentDirective);

 /** @ngInject */
 function msNewComponentController($scope, $document)
 {
  var MsNewComponent = this;

  //Data
  MsNewComponent.formVisible = false;
  MsNewComponent.form = {
   title: '',
   description: ''
  };

  //Methods
  MsNewComponent.open = open;
  MsNewComponent.close = close;
  MsNewComponent.element = [];

  //////

  /**
   * Close Trigger
   */
  $scope.$on('MsNewComponent:close', function ()
  {
   close();
  });

  /**
   * Open new component form
   */
  function open()
  {
   MsNewComponent.element.addClass('form-visible');
   MsNewComponent.element.find('textarea').focus();
   $document.on('click', outSideClick);
  }

  /**
   * Close new component form
   */
  function close()
  {
   MsNewComponent.element.removeClass('form-visible');
   $scope.$broadcast('MsNewComponent:closed');

   $document.off('click', outSideClick);
  }

  /**
   * Click Outside Event Handler
   * @param event
   */
  function outSideClick(event)
  {
   var isChild = MsNewComponent.element.has(event.target).length > 0;
   var isSelf = MsNewComponent.element[0] === event.target;
   var isMenu = angular.element(event.target).closest('md-menu-content').length > 0;
   var isCalendar = angular.element(event.target).closest('md-calendar').length > 0 || angular.element(event.target).closest('.md-datepicker-calendar-pane').length > 0 || angular.element(event.target).hasClass('md-scroll-mask');

   var isInside = isChild || isSelf || isMenu || isCalendar;

   if (!isInside)
   {
    close();
   }
  }
 }

 /** @ngInject */
 function msNewComponentDirective()
 {
  return {
   restrict: 'E',
   controller: 'msNewComponentController',
   controllerAs: 'MsNewComponent',
   scope: {
    components: '=gbComponents',
    parentComponentId: '=gbParentComponentId'
   },
   templateUrl: 'src/app/main/apps/components/directives/ms-new-component/ms-new-component.html',
   link: function (scope, element, attributes, MsNewComponent)
   {
    MsNewComponent.element = element;
   }
  };
 }

 /** @ngInject */
 function msNewComponentButtonDirective()
 {
  return {
   restrict: 'EA',
   require: '^msNewComponent',
   link: function (scope, element, attributes, msNewComponent)
   {
    element.on('click', function ()
    {
     msNewComponent.open();
    });
   }
  };
 }
})();
(function ()
{
 'use strict';

 angular
         .module('app.components')
         .controller('msComponentFormController', msComponentFormController)
         .directive('msComponentForm', msComponentFormDirective);

 /** @ngInject */
 function msComponentFormController(level_categories, $scope, $rootScope, ComponentService, msUtils, $mdDialog, $timeout, LabelsService)
 {
  var MsComponentForm = this;
  var components = ComponentService.data;

  //Data
  MsComponentForm.selectedApp = [];
  MsComponentForm.privacy = {
   value: level_categories.privacy,
   selected: {}
  }
  MsComponentForm.type = '';
  MsComponentForm.defaultComponent = {
   'id': '',
   'title': '',
   'description': '',
   'archive': false,
   'image': '',
   'color': '',
   'time': new Date(),
   'reminder': null,
   'checklist': [],
   'labels': []
  };
  MsComponentForm.componentId = '';
  MsComponentForm.editNode = {};
  MsComponentForm.newCheckListItem = '';
  MsComponentForm.checkListForm = false;
  MsComponentForm.labels = LabelsService.data;

  MsComponentForm.ngFlowOptions = {
   singleFile: true
           // You can configure the ngFlow from here
           /*target                   : 'api/media/image',
            chunkSize                : 15 * 1024 * 1024,
            maxChunkRetries          : 1,
            simultaneousUploads      : 1,
            testChunks               : false,
            progressCallbacksInterval: 1000*/
  };
  MsComponentForm.ngFlow = {
   // ng-flow will be injected into here through its directive
   flow: {}
  };

  //Methods
  MsComponentForm.init = init;
  MsComponentForm.selectApp = selectApp;
  MsComponentForm.selectPrivacy = selectPrivacy;
  MsComponentForm.submit = submit;
  MsComponentForm.isNotValid = isNotValid;
  MsComponentForm.deleteComponent = deleteComponent;
  MsComponentForm.deleteImage = deleteImage;
  MsComponentForm.addChecklistItem = addChecklistItem;
  MsComponentForm.checklistFormToggle = checklistFormToggle;
  MsComponentForm.deleteCheckItem = deleteCheckItem;
  MsComponentForm.toggleArchive = toggleArchive;
  MsComponentForm.toggleInArray = msUtils.toggleInArray;
  MsComponentForm.exists = msUtils.exists;
  MsComponentForm.upload = upload;
  MsComponentForm.imageSuccess = imageSuccess;

  //////

  /**
   * Initialize
   */
  function init() {
   // If form type edit
   if (MsComponentForm.type === 'edit')
   {
    MsComponentForm.editNode = components.getById(MsComponentForm.componentId);
    MsComponentForm.component = angular.copy(MsComponentForm.editNode);
   }
   // If form type new
   else
   {
    resetForm();
    selectPrivacy(level_categories.privacy.public, "Public");
    selectApp($rootScope.commonData.activities[0]);
   }
  }

  /**
   * Select the app
   *
   * @param {type} app selected App
   * @returns {undefined}
   */
  function selectApp(app) {
   MsComponentForm.selectedApp = app;
  }

  /**
   * Select the privacy
   *
   * @param {type} id  theid of the privacy constant
   * @param {type} title thge label of the privacy
   */
  function selectPrivacy(id, title) {
   MsComponentForm.privacy.selected.id = id;
   MsComponentForm.privacy.selected.title = title;
  }

  /**
   * Watch New Component Form, reset form if it is closed
   */
  $scope.$on('MsNewComponent:closed', function ()
  {
   resetForm();
  });

  /**
   * Reset Form
   */
  function resetForm()
  {
   $scope.$evalAsync(function ()
   {
    MsComponentForm.component = angular.copy(MsComponentForm.defaultComponent);
    MsComponentForm.checkListForm = false;
   });
  }

  /**
   * Submit Form
   */
  function submit()
  {
   if (MsComponentForm.type === 'new')
   {
    create();
   } else if (MsComponentForm.type === 'edit')
   {
    save();
   }
  }

  /**
   * Add new component
   */
  function create()
  {
   if (isNotValid())
   {
    return;
   }

   // Set default values
   MsComponentForm.component.parentComponentId = $scope.parentComponentId;
   MsComponentForm.component.typeId = MsComponentForm.selectedApp.id;
   MsComponentForm.component.privacyId = MsComponentForm.privacy.selected.id;

   // Add the component
   ComponentService.createComponent(angular.copy(MsComponentForm.component)).then(function (response) {
    $scope.components.unshift(response);
   });

   // Reset the current component to an empty one
   MsComponentForm.component = angular.copy(MsComponentForm.defaultComponent);

   $scope.$emit('MsNewComponent:close');
  }

  /**
   * Save Component
   */
  function save()
  {
   if (isNotValid())
   {
    return;
   }

   // Update the component
   ComponentService.updateComponent(MsComponentForm.component);

   // Hide the dialog
   $mdDialog.hide();
  }

  /**
   * Delete Component Image
   */
  function deleteImage()
  {
   MsComponentForm.component.image = '';
  }

  /**
   * Toggle Archive State
   */
  function toggleArchive()
  {
   if (MsComponentForm.type === 'new')
   {
    MsComponentForm.component.archive = true;
    submit();
   } else if (MsComponentForm.type === 'edit')
   {
    MsComponentForm.component.archive = !MsComponentForm.component.archive;
    save();
   }
  }

  /**
   * One of the image, title, description, checklist inputs are should be exist.
   * @returns {boolean}
   */
  function isNotValid()
  {
   return MsComponentForm.component.image === '' && MsComponentForm.component.title === '' && MsComponentForm.component.description === '' && MsComponentForm.component.checklist.length === 0;
  }

  /**
   * Delete Component
   * @param ev
   */
  function deleteComponent(ev)
  {
   var confirm = $mdDialog.confirm()
           .title('Are you sure want to delete the component?')
           .htmlContent('the component will be deleted permanently.')
           .ariaLabel('delete component')
           .targetEvent(ev)
           .ok('OK')
           .cancel('CANCEL');

   $mdDialog.show(confirm).then(function ()
   {
    ComponentService.deleteComponent(MsComponentForm.component);
   });
  }

  /**
   * Add Checklist Item
   */
  function addChecklistItem()
  {
   if (MsComponentForm.newCheckListItem === '')
   {
    return;
   }

   MsComponentForm.component.checklist.push({
    checked: false,
    text: MsComponentForm.newCheckListItem
   });

   MsComponentForm.newCheckListItem = '';

   focusChecklistInput();
  }

  /**
   * Checklist form toggle
   */
  function checklistFormToggle()
  {
   $scope.$evalAsync(function ()
   {
    MsComponentForm.checkListForm = !MsComponentForm.checkListForm;

    $timeout(function ()
    {
     focusChecklistInput();
    });
   });
  }

  /**
   * Delete check item
   * @param item
   */
  function deleteCheckItem(item)
  {
   MsComponentForm.component.checklist.splice(MsComponentForm.component.checklist.indexOf(item), 1);
   focusChecklistInput();
  }

  /**
   * Focus checklist input
   */
  function focusChecklistInput()
  {
   angular.element('#new-checklist-item-input').focus();
  }

  /**
   * Upload
   */
  function upload()
  {
   // Set headers
   MsComponentForm.ngFlow.flow.opts.headers = {
    'X-Requested-With': 'XMLHttpRequest',
    //'X-XSRF-TOKEN'    : $cookies.get('XSRF-TOKEN')
   };

   MsComponentForm.ngFlow.flow.upload();
  }

  /**
   * Image Success
   * @param file
   * @param message
   */
  function imageSuccess(file, message)
  {
   var fileReader = new FileReader();

   fileReader.readAsDataURL(file.file);

   fileReader.onload = function (event)
   {
    file.url = event.target.result;

    $scope.$evalAsync(function ()
    {
     MsComponentForm.component.image = file.url;
    });
   };

   file.type = 'image';
  }

  /**
   * Array prototype
   *
   * Get by id
   *
   * @param value
   * @returns {T}
   */
  Array.prototype.getById = function (value)
  {
   return this.filter(function (x)
   {
    return x.id === value;
   })[0];
  };

 }

 /** @ngInject */
 function msComponentFormDirective()
 {
  return {
   restrict: 'EA',
   controller: 'msComponentFormController',
   controllerAs: 'MsComponentForm',
   scope: {
    components: '=gbComponents',
    parentComponentId: '=gbParentComponentId',
    componentType: '=',
    componentId: '='
   },
   templateUrl: 'src/app/main/apps/components/directives/ms-component-form/ms-component-form.html',
   link: function (scope, element, attributes, MsComponentForm)
   {
    // Type
    MsComponentForm.type = scope.componentType;

    // Component id
    if (angular.isDefined(scope.componentId))
    {
     MsComponentForm.componentId = scope.componentId;
    }

    MsComponentForm.init();

   }
  };
 }
})();
(function ()
{
 'use strict';

 angular
         .module('app.explorer')
         .controller('ComponentStoryDialogController', ComponentStoryDialogController);

 /** @ngInject */
 function ComponentStoryDialogController(add_component_tabs, level_categories, $state, $document, $mdDialog, fuseTheming, fuseGenerator, msUtils, ComponentService, MentorshipService, componentId) {

  var vm = this;

//Data
  vm.storyTabIndex = 0;
  vm.startIndices = [];
  vm.component = [];
  vm.currentComponent = [];
  vm.downDisabled = false;
//Method
  vm.closeDialog = closeDialog;
  vm.getComponents = getComponents;
  vm.goUp = goUp;
  vm.goDown = goDown;
  vm.goLeft = goLeft;
  vm.goRight = goRight;

  init();

  /**
   * Close Dialog
   */
  function closeDialog()
  {
   $mdDialog.hide();
  }

  function getComponents(id, listType, depth, up) {
   ComponentService.getComponent(id, listType, depth).then(function (data) {
    if (data.components.length > 0) {
     vm.component = data;
     if (up) {
      vm.storyTabIndex = vm.startIndices.length > 0 ? vm.startIndices.pop() : 0;
     } else {
      vm.startIndices.push(vm.storyTabIndex);
      vm.storyTabIndex = 0;
     }
     vm.currentComponent = vm.component.components[vm.storyTabIndex];
     vm.downDisabled = false;
    } else {
     vm.downDisabled = true;
    }
   });
  }

  function goUp() {
   var id = vm.component.parent_component_id;
   if (id) {
    getComponents(id, 6, 1, true);
   }
   // vm.storyTabIndex = 0;
  }

  function goDown() {
   if (vm.component.components.length > 0) {
    var id = vm.component.components[vm.storyTabIndex].id;
    if (id) {
     getComponents(id, 6, 1);
    }
    // vm.storyTabIndex = 0;
   }
  }

  function goLeft() {
   if (vm.storyTabIndex !== 0) {
    vm.storyTabIndex--;
    vm.currentComponent = vm.component.components[vm.storyTabIndex];
    vm.downDisabled = false;
   }
  }

  function goRight() {
   if (vm.storyTabIndex < vm.component.components.length) {
    vm.storyTabIndex++;
    vm.currentComponent = vm.component.components[vm.storyTabIndex];
    vm.downDisabled = false;
   }
  }

  /**
   * Inititialization
   *
   */
  function init() {
   getComponents(componentId, 6, 1);
  }

 }
})();
(function ()
{
 'use strict';

 angular
         .module('app.components')
         .controller('ComponentSectionDialogController', ComponentSectionDialogController);

 /** @ngInject */
 function ComponentSectionDialogController(add_component_tabs, level_categories, $scope, $document, $mdDialog, fuseTheming, fuseGenerator, msUtils, DialogService, ComponentService, componentId, section) {

  var vm = this;

  // Data
  vm.tabs = add_component_tabs;
  vm.privacy = level_categories.privacy;
  vm.board = [];
  vm.component = section; //vm.board.components.getById(cardId);
  vm.newLabelColor = 'red';
  vm.members = vm.board.members;
  vm.labels = vm.board.labels;

  // Methods
  vm.createComponent = createComponent;
  vm.openComponentDialog = DialogService.openComponentDialog;
  vm.updateComponentDescription = updateComponentDescription;
  vm.updateComponentBackground = updateComponentBackground;
  vm.palettes = fuseTheming.getRegisteredPalettes();
  vm.rgba = fuseGenerator.rgba;
  vm.toggleInArray = msUtils.toggleInArray;
  vm.exists = msUtils.exists;
  vm.closeDialog = closeDialog;
  // vm.getCardList = getCardList;
  vm.removeCard = removeCard;
  /* Attachment */
  vm.toggleCoverImage = toggleCoverImage;
  vm.removeAttachment = removeAttachment;
  /* Labels */
  vm.labelQuerySearch = labelQuerySearch;
  vm.filterLabel = filterLabel;
  vm.addNewLabel = addNewLabel;
  vm.removeLabel = removeLabel;
  /* Members */
  vm.memberQuerySearch = memberQuerySearch;
  vm.filterMember = filterMember;
  /* Checklist */
  vm.updateCheckedCount = updateCheckedCount;
  vm.addCheckItem = addCheckItem;
  vm.removeChecklist = removeChecklist;
  vm.removeChecklistItem = removeChecklistItem;
  vm.createCheckList = createCheckList;
  /* Comment */
  vm.addNewComment = addNewComment;

  init();

  //////////


  /**
   * Create a new component
   *
   * @param {type} component a parent component
   */
  function createComponent(component) {
   if (!component.newComponent.title) {
    return;
   }
   var data = {
    title: component.newComponent.title,
    description: "",
    parentComponentId: componentId, //parent component
    typeId: component.id,
    privacyId: vm.privacy.public
   };
   ComponentService.createComponent(data).then(function (response) {
    component.components.push(response);
    component.newComponent.title = "";
   });
  }

  /**
   * Update a component title and background
   *
   * @param {type} component to ve updated
   */
  function updateComponentDescription(component) {
   if (!component.title) {
    return;
   }
   var data = {
    componentId: component.id,
    title: component.title,
    description: component.description
   };
   ComponentService.updateComponentDescription(data).then(function (response) {
    // component.components.push(response);
   });
  }

  /**
   * Update a component background
   *
   * @param {type} component to ve updated
   */
  function updateComponentBackground(component) {
   var data = {
    componentId: component.id,
    backgroundColor: component.background_color
   };
   ComponentService.updateComponentBackground(data).then(function (response) {
    // component.components.push(response);
   });
  }

  /**
   * Close Dialog
   */
  function closeDialog()
  {
   $mdDialog.hide();
  }

  /**
   * Get Card List
   */
  function getCardList()
  {
   var response;
   for (var i = 0, len = vm.board.lists.length; i < len; i++)
   {
    if (vm.board.lists[i].idCards.indexOf(vm.component.id) > -1)
    {
     response = vm.board.lists[i];
     break;
    }
   }
   return response;
  }

  /**
   * Remove card
   *
   * @param ev
   */
  function removeCard(ev)
  {
   var confirm = $mdDialog.confirm({
    title: 'Remove Card',
    parent: $document.find('#'),
    textContent: 'Are you sure want to remove card?',
    ariaLabel: 'remove card',
    targetEvent: ev,
    clickOutsideToClose: true,
    escapeToClose: true,
    ok: 'Remove',
    cancel: 'Cancel'
   });

   $mdDialog.show(confirm).then(function ()
   {
    var cardList = getCardList();

    cardList.idCards.splice(cardList.idCards.indexOf(vm.component.id), 1);

    vm.board.components.splice(vm.board.components.indexOf(vm.component), 1);

   }, function ()
   {
    // Canceled
   });
  }

  /**
   * Toggle cover image
   *
   * @param attachmentId
   */
  function toggleCoverImage(attachmentId)
  {
   if (attachmentId === vm.component.idAttachmentCover)
   {
    vm.component.idAttachmentCover = null;
   } else
   {
    vm.component.idAttachmentCover = attachmentId;
   }
  }

  /**
   * Remove attachment
   *
   * @param item
   */
  function removeAttachment(item)
  {
   if (vm.component.idAttachmentCover === item.id)
   {
    vm.component.idAttachmentCover = '';
   }
   vm.component.attachments.splice(vm.component.attachments.indexOf(item), 1);
  }

  /**
   * Add label chips
   *
   * @param query
   * @returns {filterFn}
   */
  function labelQuerySearch(query)
  {
   return query ? vm.labels.filter(createFilterFor(query)) : [];
  }

  /**
   * Label filter
   *
   * @param label
   * @returns {boolean}
   */
  function filterLabel(label)
  {
   if (!vm.labelSearchText || vm.labelSearchText === '')
   {
    return true;
   }

   return angular.lowercase(label.name).indexOf(angular.lowercase(vm.labelSearchText)) >= 0;
  }

  /**
   * Add new label
   */
  function addNewLabel()
  {
   vm.board.labels.push({
    id: msUtils.guidGenerator(),
    name: vm.newLabelName,
    color: vm.newLabelColor
   });

   vm.newLabelName = '';
  }

  /**
   * Remove label
   */
  function removeLabel()
  {
   var arr = vm.board.labels;
   arr.splice(arr.indexOf(arr.getById(vm.editLabelId)), 1);

   angular.forEach(vm.board.components, function (card)
   {
    if (card.idLabels && card.idLabels.indexOf(vm.editLabelId) > -1)
    {
     card.idLabels.splice(card.idLabels.indexOf(vm.editLabelId), 1);
    }
   });

   vm.newLabelName = '';
  }

  /**
   * Add member chips
   *
   * @param query
   * @returns {Array}
   */
  function memberQuerySearch(query)
  {
   return query ? vm.members.filter(createFilterFor(query)) : [];
  }

  /**
   * Member filter
   *
   * @param member
   * @returns {boolean}
   */
  function filterMember(member)
  {
   if (!vm.memberSearchText || vm.memberSearchText === '')
   {
    return true;
   }

   return angular.lowercase(member.name).indexOf(angular.lowercase(vm.memberSearchText)) >= 0;
  }

  /**
   * Update check list stats
   * @param list
   */
  function updateCheckedCount(list)
  {
   var checkItems = list.checkItems;
   var checkedItems = 0;
   var allCheckedItems = 0;
   var allCheckItems = 0;

   angular.forEach(checkItems, function (checkItem)
   {
    if (checkItem.checked)
    {
     checkedItems++;
    }
   });

   list.checkItemsChecked = checkedItems;

   angular.forEach(vm.component.checklists, function (item)
   {
    allCheckItems += item.checkItems.length;
    allCheckedItems += item.checkItemsChecked;
   });

   vm.component.checkItems = allCheckItems;
   vm.component.checkItemsChecked = allCheckedItems;
  }

  /**
   * Add checklist item
   *
   * @param text
   * @param checkList
   */
  function addCheckItem(text, checkList)
  {
   if (!text || text === '')
   {
    return;
   }

   var newCheckItem = {
    'name': text,
    'checked': false
   };

   checkList.checkItems.push(newCheckItem);

   updateCheckedCount(checkList);
  }

  /**
   * Remove checklist
   *
   * @param item
   */
  function removeChecklist(item)
  {
   vm.component.checklists.splice(vm.component.checklists.indexOf(item), 1);

   angular.forEach(vm.component.checklists, function (list)
   {
    updateCheckedCount(list);
   });
  }

  /**
   * Remove checklist Item
   *
   * @param item
   */
  function removeChecklistItem(item, list)
  {
   list.splice(list.indexOf(item), 1);

   angular.forEach(vm.component.checklists, function (list)
   {
    updateCheckedCount(list);
   });
  }

  /**
   * Create checklist
   */
  function createCheckList()
  {
   vm.component.checklists.push({
    id: msUtils.guidGenerator(),
    name: vm.newCheckListTitle,
    checkItemsChecked: 0,
    checkItems: []
   });

   vm.newCheckListTitle = '';
  }

  /**
   * Add new comment
   *
   * @param newCommentText
   */
  function addNewComment(newCommentText)
  {
   var newComment = {
    idMember: '36027j1930450d8bf7b10158',
    message: newCommentText,
    time: 'now'
   };

   vm.component.comments.unshift(newComment);
  }

  /**
   * Filter for chips
   *
   * @param query
   * @returns {filterFn}
   */
  function createFilterFor(query)
  {
   var lowercaseQuery = angular.lowercase(query);
   return function filterFn(item)
   {
    return angular.lowercase(item.name).indexOf(lowercaseQuery) >= 0;
   };
  }

  function init() {
   ComponentService.getComponentsByType(componentId, section.id).then(function (data) {
    vm.component.components = data;
   });
  }

  $scope.$watch(
          "vm.component.background_color",
          function handleBackgroundChange(newValue, oldValue) {
           if (newValue && oldValue && newValue !== oldValue) {
            updateComponentBackground(vm.component);
            console.log("vm.fooCount:", newValue);
           }
          }
  );
 }
})();
