(function ()
{
 'use strict';

 angular
         .module('app.explorer')
         .factory('DialogService', DialogService);

 /** @ngInject */
 function DialogService($mdDialog, $document)
 {
  var service = {
   openCardDialog: openCardDialog,
   openAddComponentDialog: openAddComponentDialog,
   openAddComponentsDialog: openAddComponentsDialog,
   openComponentDialog: openComponentDialog,
   openComponentSectionDialog: openComponentSectionDialog,
   openComponentNotesDialog: openComponentNotesDialog,
   startComponentStoryDialog: startComponentStoryDialog
  };

  //////////

  // ******************************
  // Internal methods
  // ******************************

  /**
   * Open card dialog
   *
   * @param ev
   * @param cardId
   */
  function openCardDialog(ev, cardId) {
   $mdDialog.show({
    templateUrl: 'src/app/main/apps/explorer/dialogs/card/card-dialog.html',
    controller: 'ExplorerCardDialogController',
    controllerAs: 'vm',
    parent: $document.find('#explorer'),
    targetEvent: ev,
    clickOutsideToClose: false,
    escapeToClose: true,
    locals: {
     cardId: cardId
    }
   });
  }

  /**
   * Open Add Component dialog
   *
   * @param ev the event which triggered the dialog
   * @param startTabIndex an initial selected tab index
   * @param preselectedData data coming from external diaolog selection tab
   *
   */
  function openAddComponentDialog(ev, componentId, startTabIndex, preselectedData) {
   $mdDialog.show({
    templateUrl: 'src/app/main/apps/explorer/dialogs/add-component/add-component-dialog.html',
    controller: 'ExplorerAddComponentDialogController',
    controllerAs: 'vm',
    parent: angular.element($document.body),
    targetEvent: ev,
    clickOutsideToClose: false,
    escapeToClose: true,
    //disableParentScroll: false,
    locals: {
     startTabIndex: startTabIndex,
     preselectedData: preselectedData,
     componentId: componentId
    }
   });
  }

  /**
   *  Adds components in bulk
   *
   * @param {type} ev event of the dialog
   * @param {type} componentId the starting parent component id
   * @returns {undefined}
   */
  function openAddComponentsDialog(ev, componentId, contents) {
   $mdDialog.show({
    templateUrl: 'src/app/main/apps/explorer/dialogs/add-components/add-components-dialog.html',
    controller: 'ExplorerAddComponentsDialogController',
    controllerAs: 'vm',
    // parent: $document.find('#explorer'),
    targetEvent: ev,
    clickOutsideToClose: false,
    escapeToClose: true,
    locals: {
     componentId: componentId,
     contents: contents
    }
   });
  }

  /**
   * Open component as a component
   *
   * @param ev  the event which triggered the dialog
   * @param componentId the component Id
   */
  function openComponentDialog(ev, componentId) {
   $mdDialog.show({
    templateUrl: 'src/app/main/apps/components/dialogs/component/component-dialog.html',
    //parent: $document.find('#components'),
    controller: 'ComponentDialogController',
    controllerAs: 'vm',
    targetEvent: ev,
    clickOutsideToClose: true,
    escapeToClose: true,
    locals: {
     componentId: componentId
    }
   });
  }

  /**
   * Open component section for editing purposes
   *
   * @param ev  the event which triggered the dialog
   * @param componentId the component Id
   * @param section the section
   */
  function openComponentSectionDialog(ev, componentId, section) {
   $mdDialog.show({
    templateUrl: 'src/app/main/apps/components/dialogs/component-section/component-section-dialog.html',
    //parent: $document.find('#components'),
    controller: 'ComponentSectionDialogController',
    controllerAs: 'vm',
    targetEvent: ev,
    clickOutsideToClose: true,
    escapeToClose: true,
    locals: {
     componentId: componentId,
     section: section
    }
   });
  }

  /**
   * Open component note for editing purposes
   *
   * @param ev  the event which triggered the dialog
   * @param componentId the component Id
   * @param note the note
   */
  function openComponentNotesDialog(ev, componentId, notes) {
   $mdDialog.show({
    templateUrl: 'src/app/main/apps/components/dialogs/component-notes/component-notes-dialog.html',
    //parent: $document.find('#components'),
    controller: 'ComponentNotesDialogController',
    controllerAs: 'vm',
    targetEvent: ev,
    clickOutsideToClose: true,
    escapeToClose: true,
    locals: {
     componentId: componentId,
     notes: notes
    }
   });
  }

  /**
   * Starts a story slide from componentId
   *
   * @param {type} ev
   * @param {type} componentId
   * @returns {undefined}
   */
  function startComponentStoryDialog(ev, componentId) {
   $mdDialog.show({
    templateUrl: 'src/app/main/apps/components/dialogs/component-story/component-story-dialog.html',
    controller: 'ComponentStoryDialogController',
    controllerAs: 'vm',
    // parent: $document.find('#explorer'),
    targetEvent: ev,
    clickOutsideToClose: false,
    escapeToClose: true,
    locals: {
     componentId: componentId
    }
   });
  }

  return service;
 }
})();
(function ()
{
    'use strict';

    angular
        .module('app.chat', [])
        .config(config);

    /** @ngInject */
    function config($stateProvider, $translatePartialLoaderProvider, msApiProvider, msNavigationServiceProvider)
    {

        // State
        $stateProvider.state('app.chat', {
            url    : '/chat',
            views  : {
                'content@app': {
                    templateUrl: 'src/app/main/apps/chat/chat.html',
                    controller : 'ChatController as vm'
                }
            },
            resolve: {
                Contacts: function (msApi)
                {
                    return msApi.resolve('chat.contacts@get');
                },
                User    : function (msApi)
                {
                    return msApi.resolve('chat.user@get');
                }
            }
        });

        // Translation
        $translatePartialLoaderProvider.addPart('src/app/main/apps/chat');

        // Api
        // Contacts data must be alphabatically ordered.
        msApiProvider.register('chat.contacts', ['src/app/data/chat/contacts.json']);
        msApiProvider.register('chat.chats', ['src/app/data/chat/chats/:id.json']);
        msApiProvider.register('chat.user', ['src/app/data/chat/user.json']);

        // Navigation
        msNavigationServiceProvider.saveItem('apps.chat', {
            title : 'Chat',
            icon  : 'icon-hangouts',
            state : 'app.chat',
            badge : {
                content: 13,
                color  : '#09d261'
            },
            weight: 5
        });
    }

})();
(function ()
{
    'use strict';

    angular
        .module('app.chat')
        .factory('ChatsService', ChatsService);

    /** @ngInject */
    function ChatsService($q, msApi)
    {
        
        var service = {
            chats         : {},
            contacts      : [],
            getContactChat: getContactChat
        };

        /**
         * Get contact chat from the server
         *
         * @param contactId
         * @returns {*}
         */
        function getContactChat(contactId)
        {

            // Create a new deferred object
            var deferred = $q.defer();

            // If contact doesn't have lastMessage, create a new chat
            if ( !service.contacts.getById(contactId).lastMessage)
            {
                service.chats[contactId] = [];

                deferred.resolve(service.chats[contactId]);
            }

            // If the chat exist in the service data, do not request
            if ( service.chats[contactId] )
            {
                deferred.resolve(service.chats[contactId]);
                
                return deferred.promise;
            }

            // Request the chat with the contactId
            msApi.request('chat.chats@get', {id: contactId},

                // SUCCESS
                function (response)
                {
                    // Attach the chats
                    service.chats[contactId] = response.data;

                    // Resolve the promise
                    deferred.resolve(service.chats[contactId]);
                },

                // ERROR
                function (response)
                {
                    deferred.reject(response);
                }
            );

            return deferred.promise;
        }

        /**
         * Array prototype
         *
         * Get by id
         *
         * @param value
         * @returns {T}
         */
        Array.prototype.getById = function (value)
        {
            return this.filter(function (x)
            {
                return x.id === value;
            })[0];
        };
        return service;
    }
})();
